<template>
	<div class="pannelPadding-10">
		<div class="pannelPaddingBg-10">
			<div>
				<div flex>
					<span class="query-title" style="padding:2px 4px;margin-right:10px">高级查询:</span>
					<el-input
						style="width:500px"
						class="search-input"
						placeholder="请输入项目编号/项目名称/合同编号/项目经理/学校"
						prefix-icon="el-icon-search"
						size="mini"
						v-model="filterData.keyword"
						@change="handleSearch"
					></el-input>
					&#x3000;
					<el-button type="primary" size="mini" @click="handleSearch">搜索</el-button>
					<el-button type="primary" size="mini" @click="handleExport">导出</el-button>
				</div>

				<div>
					<span class="query-title">服务对象:</span>
					<p class="query-list" style="width:90%">
						<span v-for="fwdx in fwdxList" :class="{ 'bg-active': fwdx.id == filterData.fwdx }" @click="CheckGcdz(fwdx.id)">{{ fwdx.label }}</span>
					</p>
				</div>

				<div flex>
					<span class="query-title">服务类别:</span>
					<p class="query-list">
						<span v-for="fwlb in fwlbList" :class="{ 'bg-active': fwlb.id == filterData.fwlb }" :key="fwlb.id" @click="CheckSfsh(fwlb.id)">{{ fwlb.label }}</span>
					</p>
				</div>

				<div flex>
					<span class="query-title">服务指南:</span>
					<p class="query-list">
						<span v-for="fwzn in fwznList" :class="{ 'bg-active': fwzn.id == filterData.fwzn }" :key="fwzn.id" @click="CheckSfsh(fwzn.id)">{{ fwzn.label }}</span>
					</p>
				</div>
				<div flex>
					<span class="query-title">信息化支持:</span>
					<p class="query-list">
						<span v-for="xxhzc in xxhzcList" :class="{ 'bg-active': xxhzc.id == filterData.xxhzc }" :key="xxhzc.id" @click="CheckSfsh(xxhzc.id)">{{ xxhzc.label }}</span>
					</p>
				</div>
				<div flex>
					<span class="query-title">在建状态:</span>
					<p class="query-list">
						<span v-for="jszt in jsztList" :class="{ 'bg-active': jszt.id == filterData.jszt }" :key="jszt.id" @click="CheckSfsh(jszt.id)">{{ jszt.label }}</span>
					</p>
				</div>
			</div>
			<div class="pd-10">
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column prop="date" label="日期" width="180"></el-table-column>
					<el-table-column prop="name" label="姓名" width="180"></el-table-column>
					<el-table-column prop="address" label="地址"></el-table-column>
				</el-table>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			fwdxList: [{ id: '1', label: '对象' }], //服务对象
			fwlbList: [{ id: '1', label: '类别' }], //服务类别
			fwznList: [{ id: '1', label: '类别' }], //服务指南
			xxhzcList: [{ id: '', label: '全部' }, { id: '1', label: '有' }, { id: '0', label: '无' }],
			jsztList: [{ id: '', label: '全部' }, { id: '1', label: '在建' }, { id: '2', label: '售后' }, { id: '2', label: '服务' }],
			filterData: {
				fwlb: '',
				fwdx: '',
				fwzn:'',
				xxhzc:'',
				jszt:'',
				
			},
			tableData: [
				{
					date: '2016-05-02',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1518 弄'
				},
				{
					date: '2016-05-04',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1517 弄'
				},
				{
					date: '2016-05-01',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1519 弄'
				},
				{
					date: '2016-05-03',
					name: '王小虎',
					address: '上海市普陀区金沙江路 1516 弄'
				}
			]
		};
	},
	methods: {
		handleSearch() {},
		handleExport() {}
	}
};
</script>

<style></style>



